const Tween = require('@ui/tween');
const xtpl = require('@util/xtpl');
require('./index.css');

let htmlTemp = `<aside class="aside hide">
    <div class="aside-content">
        <nav>
            <p>{title}</p>
            <div class="aside-close">
                <i class="aside-icon-close"></i>
            </div>
        </nav>
        <section>  
        </section>
    </div>
</aside>`;

let renderTemp = `{{ if !stopRenderKey }}
    <ul class="aside-key">
        {{ each keys as k }}<li>{{ k }}</li>{{ endeach }}
    </ul>
    {{ endif }}
    {{ for data as list key }}
        {{ if !stopRenderKey }}
            <h3 data-key="{{ key }}">{{ key }}</h3>
        {{ endif }}
        <ul class="aside-list">
            {{ each list as item i}}
                <li data-idx="{{ i }}" data-key="{{ key }}">
                {{ if item[map.icon] }}
                    <img src="{{ item[map.icon] }}">
                {{ endif }}
                {{ item[map.name] }}
                </li>
            {{ endeach }}
        </ul>
    {{ endfor }}
`;

class Select{
    /**
     * @param opt{Object} 
     * @param opt.stopRenderKey {Boolean} 是否渲染快捷锚点
     * @param opt.title {String} 渲染标题
     */
    
    /**
     * 渲染数据格式
     *  [{
            icon  : '',  
            name  : '',  
            key   : ''   //首字母
        }]
     */
    constructor(opt){
        this.stopRenderKey = opt.stopRenderKey;  // true/false
        this.keys = [];
        this.data = {};
        this.renderMap = {
            key   : 'key',
            name  : 'name',
            icon  : 'icon'
        };

        this.$wrap = $(htmlTemp.replace('{title}', opt.title));
        this.render = xtpl(renderTemp);
        $('body').append(this.$wrap);

        this.$content = this.$wrap.find('.aside-content');
        this.$section = this.$wrap.find('section');
        
        this.onTapEvent = null;
        this.bindEvent();
    }
    onTap(fn){
        this.onTapEvent = fn;
        return this;
    }
    setRenderMap(obj){
        this.renderMap.key  = obj.key || 'key';
        this.renderMap.name = obj.name || 'name';
        this.renderMap.icon = obj.icon || 'icon';
        return this;
    }
    format(data){
        data.forEach( item => {
            let key = item[this.renderMap.key];
            if (!this.data[key]) {
                this.keys.push(key);
                this.data[key] = [];
            }
            this.data[key].push(item);
        });
        return this;
    }
    setData(data){
        this.data = {};
        this.keys.length = 0;
        this.format(data);
        let listHtml = this.render({
            stopRenderKey : this.stopRenderKey,
            keys : this.keys,
            data : this.data,
            map  : this.renderMap
        });
        this.$section.html(listHtml);
        return this;
    }
    bindEvent(){
        let me = this;
        this.$wrap.on('touchmove', function(e){
            if($(e.target).hasClass('aside')){
                return false;
            }
        });
        this.$wrap.on('click', function(e) {
            e.preventDefault();
            if ($(e.target).hasClass('aside')) {
                me.hide();
            }
        })
        this.$wrap.find('.aside-close').on('click', function(e) {
            e.preventDefault();
            // 关闭按钮
            me.hide();
        });
        this.$wrap.on('click', '.aside-key li', function(e) {
            e.preventDefault();
            // 字母锚点事件
            let that = $(this);
            let $tag = me.$wrap.find('h3[data-key="'+that.html()+'"]');
            let offset = $tag.offset(); 
            // fix ios -webkit-overflow-scrolling bug 
            new Tween().from({
                top : me.$section.scrollTop()
            }).to({
                top : offset.top - 46
            }).on('update', function(o) {
                me.$section.scrollTop(o.top);
            }).start();
        });
        this.$wrap.on('click', '.aside-list li' ,function(e) {
            e.preventDefault();
            // option 触摸事件
            let that = $(this);
            let key = that.data('key');
            let idx = that.data('idx');

            if (me.onTapEvent) {
                me.onTapEvent.call(me, me.data[key][idx]);
            }
        });
    }
    show(){
        this.$wrap.removeClass('hide');
        setTimeout(() => {
            this.$content.addClass('aside-show');
        },20);
        return this;
    }
    hide(){
        this.$content.removeClass('aside-show');
        setTimeout(() => {
            this.$wrap.addClass('hide');
        }, 300);
        return this;
    }
    destroy(){
        this.hide();
        setTimeout(() => {
            this.$wrap.remove();
        });
    }
}

module.exports = Select;



