
/**
*
* @param tpl {String} 模板字符串
* @param data {Object} 模板数据（不传或为null时返回渲染方法）
*
* @return {String} 渲染结果
* @return {Function} 渲染方法
*
*/

function parse(p0){// 指令解析
    var dir = p0.trim().replace(/[\s]+/g, ' ').split(' ');
    switch(dir[0]){
        case 'if':
            return `;if(${dir.slice(1).join(' ')}){`;
        case 'else':
            return'}else{';
        case 'elseif':
            return `}else if(${dir.slice(1).join(' ')}){`
        case 'endif':
            return '};';
        case 'each':
            return `; ${dir[1]}.forEach(function(${dir[3]}, ${dir[4]}){`;
        case 'endeach':
            return '});';
        case 'for':
            return `;(function(){ for(var ${dir[4]} in ${dir[1]}){ var ${dir[3]}=${dir[1]}[${dir[4]}];`
        case 'endfor':
            return '}}());';
        default : // 默认直接输出变量, 过滤 undefined
            //return `;$+=(typeof ${dir[0]}=='undefined'?'':${dir[0]}) ;` ;
            return `;$+=(typeof (${p0})==='undefined'?'':(${p0}));`;
    }
}
module.exports = function(tpl, data) {
    let fn = function(d){
        let i , k=[] , v=[];
        for(i in d){
            k.push(i);
            v.push(d[i]);
        }
        return (new Function(k, fn.$)).apply(d, v);
    };
    if(!fn.$){
        fn.$ = 'var $="";';
        let tpls = tpl.split('{{');
        for(let t = 0, len=tpls.length; t<len; t++){
            let p = tpls[t].split('}}');
            if (t !== 0) {
                fn.$ += parse(p[0]);
            }
            fn.$ += '$+="';
            fn.$ += p[p.length-1].replace(/\'/g, "\\'")
                                .replace(/\"/g,'\\"')
                                .replace(/\r\n/g, '\\n')
                                .replace(/\n/g, '\\n')
                                .replace(/\r/g, '\\n');
            fn.$ += '"';
        }
        fn.$ += ';return $;';
        //console.log(fn.$);
    }
    return data ? fn(data) : fn;  
};



